@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*::-webkit-scrollbar
{
    display: none;
}

*{
  scroll-behavior: smooth;
}

.App{
  min-height: 100vh;
  background-image: url(/static/media/HEG.df122cc7.png);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  color: white;
  padding-top: 130px;
  padding-bottom: 70px;
}

@media(max-width:700px){
  .App{
    padding-top: 70px;
  }
}

.pageTitle{
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-family: "Montserrat",sans-serif;
  font-size: 2vw;
  padding: 4px;
  border-radius: 50px;
  color: white;
}

@media (max-width: 1000px) {
  .pageTitle{
    font-size: 6.4vw;
  }
}
.header{
    width: 100%;
    cursor: pointer;
    position: fixed;
    display: flex;
    justify-content: center;
    text-transform: capitalize;
    background-color: gray;
    box-shadow: 0px 2px 5px rgba(49, 44, 44, 0.578);
    font-family: "Montserrat";
    font-size: 3vw;
    padding-bottom: 15px;
    color: white;
    z-index: 100;
    text-shadow: 2px 2px 8px red;
}

@media (max-width:1000px){
    .header{
        padding-top: 10px;
        font-size: 4.5vw;
    }
}
.ContentModal__landscape {
    object-fit: contain;
    border-radius: 10px;
    /* box-shadow: 1px 1px 8px black; */
}

.ContentModal__portrait {
    display: none;
    object-fit: contain;
    border-radius: 10px;
    /* box-shadow: 1px 1px 8px black; */
}

.tagline {
    padding-top: 10px;
    padding-bottom: 10px;
    align-self: center;
}

.ContentModal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    scrollbar-width: none;
}

.ContentModal::-webkit-scrollbar {
    display: none;
}

.ContentModal__about {
    padding: 10px;
    width: 95%;
    height: 90%;
    display: flex;
    flex-direction: column;
    font-family: "Roboto", sans-serif;
    justify-content: space-evenly;
    font-weight: 300;
}

.ContentModal__title {
    padding-top: 10px;
    height: 12%;
    font-size: 4vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

.ContentModal__description {
    display: flex;
    height: 40%;
    padding: 15px;
    overflow-y: scroll;
    border-radius: 20px;
    scrollbar-width: thin;
    /* FIREFOX */
    /* box-shadow: inset 0 0 5px #363333; */
    box-shadow: inset 0 0 5px #ffffff;
    text-align: justify;
}

.ContentModal__description::-webkit-scrollbar {
    display: none;
}

@media (min-width: 835px) {
    .ContentModal__landscape {
        display: none;
    }
    .ContentModal__portrait {
        display: flex;
        width: 38%;
    }
    .ContentModal {
        flex-direction: row;
        justify-content: space-around;
        padding: 10px 0;
    }
    .ContentModal__about {
        width: 58%;
        padding: 0;
        height: 100%;
    }
    .ContentModal__title {
        font-size: 3vw;
    }
    .ContentModal__description {
        font-size: 22px;
        height: 30%;
    }
}
.carouselItem
{
    display: flex;
    flex-direction: column;
    object-fit: contain;
    padding: 10px;
}

.carouselItem__img
{
    border-radius: 10px;
    margin-bottom: 5px;
    /* box-shadow: 0px 0px 5px black; */
    box-shadow: 1px 1px 8px black;
}

.carouselItem__txt
{
    font-family: "Roboto",sans-serif;
    font-weight: 300;
}
.media {
    display: flex;
    flex-direction: column;
    width: 200px;
    padding: 5px;
    margin: 5px 0;
    background: linear-gradient(rgba(211, 211, 211, 0.375), rgba(169, 169, 169, 0.499));
    /* box-shadow: 3px 3px 2px rgba(252, 252, 252, 0.533); */
    border-radius: 10px;
    position: relative;
    font-family: "Montserrat", sans-serif;
}

.media:hover {
    background-color: rgba(255, 255, 255, 0.692);
    color: black;
}

@media (max-width: 550px) {
    .media {
        width: 46%;
    }
}

.poster {
    border-radius: 10px;
    box-shadow: 1px 1px 8px black;
}

.title {
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
}

.subTitle {
    display: flex;
    justify-content: space-between;
    padding: 0 2px;
    padding-bottom: 3px;
}
.trending{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
input{
    color: white;
}
