.header{
    width: 100%;
    cursor: pointer;
    position: fixed;
    display: flex;
    justify-content: center;
    text-transform: capitalize;
    background-color: gray;
    box-shadow: 0px 2px 5px rgba(49, 44, 44, 0.578);
    font-family: "Montserrat";
    font-size: 3vw;
    padding-bottom: 15px;
    color: white;
    z-index: 100;
    text-shadow: 2px 2px 8px red;
}

@media (max-width:1000px){
    .header{
        padding-top: 10px;
        font-size: 4.5vw;
    }
}